#root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: top;
      align-items: top;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100vw;
  height: 100vh
}

.w100 {
  width: 100%;
}

.topDiv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100vw;
  height: 100vh;
  max-height: 800px;
  max-width: 800px;
}

.header {
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
  height: 80px;
}

.gridContainer {
  -ms-flex: 1 1;
      flex: 1 1;
  display: grid;
  grid-template-columns: repeat(2, 50%);
  grid-template-rows: repeat(2, 50%);
}

.box {
  margin: 10px;
}

.box1 {
   grid-column: 1/2;
   grid-row: 1/2;
}

.box2 {
   grid-column: 2/3;
   grid-row: 1/2;
}

.box3 {
   grid-column: 1/2;
   grid-row: 2/3;
}

.box4 {
   grid-column: 2/3;
   grid-row: 2/2;
}

.button {
  text-transform: capitalize;
  text-align: center; 
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); 
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  width: 80%;
  border-radius: 5px;
  margin: 10px;
  background-color: Tan;
}


.button:hover {
   -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
           box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
   cursor: pointer;
}

table {
  text-align: left;
  font-size: 0.8rem;
  border: solid;
  width: 100%;
  -o-text-overflow: wrap;
     text-overflow: wrap;
}

div.over {
  overflow-y: scroll;
}




