.card {
    text-align: center; 
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); 
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    background-color: Tan;
    height: 100%;
}

h3 {
	margin: 0px;
	height: 3rem;
	text-transform: capitalize;
}

table {
	font-size: 0.8rem;
	border: solid;
	width: 100%;
	-o-text-overflow: wrap;
	   text-overflow: wrap;
}


